import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import { defaultProductCard } from '../../../images';
import ProductDetails from './ProductDetails';
import axios from 'axios';

const ProductDetailsContainer = ({
    context,
}) => {
    const numDetaliProductLocalStore = window.localStorage.getItem('numDetaliProduct');
    const {
        dispatch,
        recommendedProductsStore,
        productYouAlredyWatchStore,
        productDetails,
        reviewsApplications,
        currency,
        tempProductId
    } = useStoreon('currency', 'recommendedProductsStore', 'productYouAlredyWatchStore', 'productDetails', 'reviewsApplications', 'tempProductId');

    const {
        role_configuration,
        recommended_price,
        reviews_statistic,
        reviews_count,
        breadcrumbs,
        page_info,
        reviews,
    } = context;
    const {
        in_stock_count,
        in_cart_count,
        is_bestseller,
        is_collection,
        collections,
        is_closeout,
        is_in_stock,
        product_rc,
        minimum_rc,
        is_liked,
        article,
        content,
        is_new,
        review,
        prices,
        colors,
        sizes,
        media,
        brand,
        title,
        extra,
        id,

    } = productDetails;

    const location = useLocation();
    const navigate = useNavigate();

    const { role, status } = context.profile;
    const {
        allReviews,
        dataReviewProductCount,
        dataReviewServiceCount,
        product_reviews,
        service_reviews,
        count_is_photos,
        pageReviewProduct,
        pageReviewService,
    } = reviewsApplications;
    const countRaiting = review.all_count;
    const allCountPercent = review.all_count_percent;

    const profileId = context.profile?.id;
    const { delivery_condition } = role_configuration;
    // let tempProductId;

    useEffect(()=>{
        console.log('---------------------Вы попали на страницу товара',id)
        document.querySelector('.goto').scrollIntoView({ block: 'start', behavior: 'smooth' });
        // if(tempProductId !== id) 
        console.log(tempProductId, '-----',id)
        return function cleanup(){  
            console.log('---------------------Вы ПОКИНУЛИ страницу товара--------------------',window.location.href.includes('/product-'))
            if(!window.location.href.includes('/product-')) dispatch('getProductDetailsZerro');

        }
    },[id])


    const handleChooseProduct = (productId, colorId, sizeId) => {
        const timer = setTimeout(()=>{
            document.querySelector('.goto').scrollIntoView({ block: 'start', behavior: 'smooth' });
            return ()=> clearTimeout(timer);
          }, 100)
        const params = {
            productId: productId,
            color: colorId,
            size: sizeId,
            collection: null,
            // source:source
        }
        dispatch('getProductDetails', params)
    }

    const handlerOpenListCollection = (collections, title) => {
        dispatch('openModalCollections', {
            collections,
            title
        })
    }

    const addLikeProductCard = (id) => {
        const params = {
            id: id,
            whereLike: 'detail-product',
            pathname: location.pathname,
            redirectTo: (path) => {
                const timerTimeout = setTimeout(() => {
                    navigate(path);
                    return () => clearTimeout(timerTimeout);
                }, 500)
            }
        }
        dispatch('addWishList', params)
    }

    const removeLikeProductCard = (id) => {
        const params = {
            id: id,
            whereLike: 'detail-product',
            pathname: location.pathname,
            redirectTo: (path) => {
                const timerTimeout = setTimeout(() => {
                    navigate(path);
                    return () => clearTimeout(timerTimeout);
                }, 500)
            }
        }
        dispatch('removeWishList', params)
    }

    const openTableSize = () => {
        dispatch('openModalTableSize')
    }

    const test = () => {
        dispatch('test')
    }

    return (
        <ProductDetails
            test={test}
            isLoadingDataProduct={productDetails.isLoadingProductData}
            dataReviewProductCount={dataReviewProductCount}
            delivery_condition={delivery_condition}
            defaultProductCard={defaultProductCard}
            allCountPercent={allCountPercent}
            pageReviewProduct={pageReviewProduct}
            recommended_price={recommended_price}
            reviews_statistic={reviews_statistic}
            product_reviews={product_reviews}
            in_stock_count={in_stock_count}
            youAlredyWatch={productYouAlredyWatchStore}
            product_rcAmount={minimum_rc}
            is_bestseller={is_bestseller}
            in_cart_count={in_cart_count}
            is_collection={is_collection}
            reviews_count={reviews_count}
            countRaiting={countRaiting}
            breadcrumbs={breadcrumbs}
            is_closeout={is_closeout}
            is_in_stock={is_in_stock}
            collections={collections}
            recommended={recommendedProductsStore}
            product_rc={product_rc}
            profileId={profileId}
            is_liked={is_liked}
            currency={currency}
            article={article}
            content={content}
            colors={colors}
            status={status}
            is_new={is_new}
            prices={prices}
            productId={id}
            sizes={sizes}
            media={media}
            brand={brand}
            title={title}
            extra={extra}
            role={role}
            // url={url}
            handleChooseProduct={handleChooseProduct}
            handlerOpenListCollection={handlerOpenListCollection}
            addLikeProductCard={addLikeProductCard}
            removeLikeProductCard={removeLikeProductCard}
            openTableSize={openTableSize}
        />
    )
}

export default React.memo(ProductDetailsContainer);
