import React from "react";
import AsyncComponent from "../../../helpers/asyncComponent";
import Text from "../../../helpers/Text";
import { ROLE } from "../../../const";
import { hanger } from "../../../images";

import Breadcrumbs from "../../../Views/Breadcrumbs";
import Title from "../../../Views/Title";
import Row from "../../../Views/GridContainerBlock/Row/Row";
import Container from "../../../Views/GridContainerBlock/Container";
import BlockLeft from "../../../Views/GridContainerBlock/BlockLeft";
import BlockRight from "../../../Views/GridContainerBlock/BlockRight";
import BlockBody from "../../../Views/GridContainerBlock/BlockBody";
import DeliveryInfo from "../../../Views/ProductDetailsViews/DeliveryInfo/DeliveryInfo";
import BrandName from "../../../Views/ProductDetailsViews/BrandName/BrandName";

const AsyncLabels = AsyncComponent(() => {
  return import("../../../Views/ProductDetailsViews/Labels/Labels");
});
const AsyncPricesContainer = AsyncComponent(() => {
  return import(
    "../../../Views/ProductDetailsViews/PricesContainer/PricesContainer"
  );
});
const AsyncColorsButton = AsyncComponent(() => {
  return import("../../../Views/ProductDetailsViews/ColorsItems/ColorsItems");
});
const AsyncSizesButton = AsyncComponent(() => {
  return import("../../../Views/ProductDetailsViews/SizesItems/SizesItems");
});
const AsyncBuyoutCondition = AsyncComponent(() => {
  return import(
    "../../../Views/ProductDetailsViews/BuyoutCondition/BuyoutCondition"
  );
});
const AsyncInfoOpenCollection = AsyncComponent(() => {
  return import(
    "../../../Views/ProductDetailsViews/InfoOpenCollection/InfoOpenCollection"
  );
});
const AsyncControlButtons = AsyncComponent(() => {
  return import(
    "../../../Views/ProductDetailsViews/ControlButtons/ControlButtonsContainer.js"
  );
});

const AsyncPreviewSlider = AsyncComponent(() => {
  return import(
    "../../../Views/PreviewProduct/PreviewSlider/PreviewSliderContainer"
  );
});

const AsyncReviewsBlock = AsyncComponent(() => {
  return import("../../../Views/ReviewsBlock/ReviewsContainer");
});

const AsyncYouHaveAlreadyWatched = AsyncComponent(() => {
  return import(
    "../../../Views/YouHaveAlreadyWatched/YouHaveAlreadyWatchedContainer.js"
  );
});

const AsyncSectionRaitingAndSharingAndMyWish = AsyncComponent(() =>
  import(
    "../../../Views/ProductDetailsViews/SectionRaitingAndSharingAndMyWish/SectionRaitingAndSharingAndMyWish"
  )
);

const SceletonBlock = AsyncComponent(() =>
  import("../../../Views/SceletonBlock")
);

const SectionDescription = AsyncComponent(() =>
  import(
    "../../../Views/ProductDetailsViews/SectionDescription/SectionDescription"
  )
);
const Button = AsyncComponent(() => import("../../../Views/Button"));
const Icon = AsyncComponent(() => import("../../../Views/Icon"));

const ProductDetails = ({
  isLoadingDataProduct,
  test,
  defaultProductCard,
  dataReviewProductCount,
  delivery_condition,
  pageReviewProduct,
  recommended_price,
  allCountPercent,
  reviews_statistic,
  product_rcAmount,
  product_reviews,
  youAlredyWatch,
  in_stock_count,
  in_cart_count,
  is_bestseller,
  is_collection,
  reviews_count,
  countRaiting,
  recommended,
  is_closeout,
  breadcrumbs,
  is_in_stock,
  collections,
  product_rc,
  profileId,
  productId,
  currency,
  is_new,
  prices,
  colors,
  sizes,
  media,
  brand,
  title,
  role,
  status,
  article,
  content,
  extra,
  url,
  is_liked,

  handleChooseProduct,
  handlerOpenListCollection,
  addLikeProductCard,
  removeLikeProductCard,
  openTableSize,
}) => {
  return (
      <Container>
        <Breadcrumbs breadcrumbs={breadcrumbs} />
        {/* <button
                    onClick={test}
                > test request</button> */}
        <BlockBody>
          <BlockLeft>
            <AsyncPreviewSlider
              defaultImage={defaultProductCard}
              isLoadingDataProduct={isLoadingDataProduct}
              imageOrVideoSet={media}
            />
          </BlockLeft>
          <BlockRight>
            <Row // ? 1 информационный ряд Raiting
              addClass={"preview-product-modal__row"}
            >
              {/* // ?! ДАННЫЕ НЕ ПРИХОДЯТ, ЖДЁМ ОТ БЭКА Raiting
               */}

              <AsyncSectionRaitingAndSharingAndMyWish
                allCountPercent={allCountPercent}
                countRaiting={countRaiting}
                is_liked={is_liked}
                title={title}
                value={0}
                label={Text({ text: "reviews-es" })}
                max={5}
                productId={productId}
                addLikeProductCard={addLikeProductCard}
                removeLikeProductCard={removeLikeProductCard}
              />
            </Row>
            <Row // ? 2 информационный ряд Brand
              addClass={"preview-product-modal__row"}
            >
              {!!brand && role !== ROLE.RETAIL && role !== ROLE.UNREGISTRED ? (
                <BrandName name={brand} />
              ) : null}
            </Row>
            <Row // ? 3 информационный ряд Title
              addClass={"preview-product-modal__row"}
            >
              {!!title && title !== "title" ? (
                <Title variant={"prodpage__title"} type={"h1"}>
                  {title}
                </Title>
              ) : (
                <SceletonBlock />
              )}
            </Row>
            <Row // ? 4 информационный ряд is_now, is_besceler, is_istock ...
              addClass={"preview-product-modal__row"}
            >
              <AsyncLabels
                is_in_stock={is_in_stock}
                is_bestseller={is_bestseller}
                is_new={is_new}
                is_closeout={is_closeout}
              />
            </Row>
            <Row // ? 5 информационный ряд стоимость товара
              addClass={"preview-product-modal__row"}
            >
              <AsyncPricesContainer
                prices={prices}
                in_cart_count={in_cart_count}
                currency={currency}
                recommended_price={recommended_price}
                role={role}
              />
            </Row>
            <Row // ? 6 информационный ряд цветовая разкраска товара
              addClass={"preview-product-modal__row"}
            >
              <AsyncColorsButton
                isLoadingDataProduct={isLoadingDataProduct}
                handleChooseProduct={handleChooseProduct}
                productId={productId}
                colors={colors}
                sizes={sizes}
              />
            </Row>
            <Row // ? 6 информационный ряд цветовая разкраска товара
              addClass={"preview-product-modal__row"}
            >
              <Button
                className=""
                variant="text"
                onClick={openTableSize}
                addClass={"prodpage-sizes__btn"}
              >
                <Icon
                  slot="icon-left"
                  src={hanger}
                  className="prodpage-sizes__icon"
                  width={15}
                  height={15}
                />
                Таблица размеров
              </Button>
            </Row>

            {role === ROLE.WHOLESALE ||
            (role === ROLE.DROPSHIPPER && is_collection) ? (
              <Row // ? 7 информационный ряд c условиями выкупа
                addClass={"preview-product-modal__row"}
              >
                <AsyncBuyoutCondition
                  role={role}
                  prices={prices}
                  currency={currency}
                  product_rc={product_rc}
                  is_collection={is_collection}
                  product_rcAmount={product_rcAmount}
                />
              </Row>
            ) : null}
            <Row // ? 8 информационный ряд c размерами товара
              addClass={"preview-product-modal__row"}
            >
              <AsyncSizesButton
                isLoadingDataProduct={isLoadingDataProduct}
                handleChooseProduct={handleChooseProduct}
                in_stock_count={in_stock_count}
                productId={productId}
                colors={colors}
                sizes={sizes}
              />
            </Row>
            {role === ROLE.DROPSHIPPER ? (
              <Row // ? 9 информационный ряд кнопка для сборов только для дроперов
                addClass={"preview-product-modal__row"}
              >
                <AsyncInfoOpenCollection
                  collections={collections}
                  status={status}
                  handlerOpenListCollection={handlerOpenListCollection}
                  title={title}
                />
              </Row>
            ) : null}
            <Row // ? 10 информационный ряд c кнопками добавления и перехода к товару
              addClass={"preview-product-modal__row"}
            >
              <AsyncControlButtons
                in_stock_count={in_stock_count}
                is_collection={is_collection}
                in_cart_count={in_cart_count}
                is_in_stock={is_in_stock}
                productId={productId}
                role={role}
                url={url}
              />
            </Row>

            <Row // ? 10 информационный ряд c кнопками добавления и перехода к товару
              addClass={"preview-product-modal__row"}
            >
              <DeliveryInfo role={role} description={delivery_condition} />
            </Row>
          </BlockRight>
        </BlockBody>
        <Row // ? 12 информационный ряд c описанием товара
          addClass={"preview-product-modal__row"}
        >
          {content || extra ? (
            <SectionDescription
              content={content}
              extra={extra}
              article={article}
            />
          ) : null}
        </Row>

        <Row // ? 13 информационный ряд c отзывами о товаре
          addClass={"preview-product-modal__row"}
        >
          <AsyncReviewsBlock
            dataReviewProductCount={dataReviewProductCount}
            pageReviewProduct={pageReviewProduct}
            reviews_statistic={reviews_statistic}
            product_reviews={product_reviews}
            reviews_count={reviews_count}
            profileId={profileId}
            productId={productId}
            role={role}
          />
        </Row>

        <Row // ? 15 информационный ряд Вы уже смотрели
          addClass={"preview-product-modal__row"}
        >
          <AsyncYouHaveAlreadyWatched
            youAlredyWatch={youAlredyWatch}
            currency={currency}
            isLoadingMyWish={youAlredyWatch?.isLoadingMyWish}
            headerText={Text({ text: "already.watched" })}
            role={role}
          />
        </Row>

        <Row // ? 14 информационный ряд Рекомендуемое
          addClass={"preview-product-modal__row"}
        >
          <AsyncYouHaveAlreadyWatched
            isLoadingMyWish={recommended?.isLoadingMyWish}
            headerText={"Рекомендуемое"}
            youAlredyWatch={recommended}
            currency={currency}
            role={role}
          />
        </Row>
      </Container>
  );
};

export default React.memo(ProductDetails);
